class Room {
  constructor(
    name,
    roomIndex,
    textureUrl,
    position = new THREE.Vector3(0, 0, 0),
    euler = new THREE.Euler(0, 0, 0)
  ) {
    this.name = name;
    // Ê∑ªÂä†Á´ãÊñπ‰Ωì
    const geometry = new THREE.BoxGeometry(10, 10, 10);
    geometry.scale(1, 1, -1);
    // const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
    // const cube = new THREE.Mesh(geometry, material);
    // scene.add(cube);

    // 4_b,
    var arr = [
      `${roomIndex}_l`,
      `${roomIndex}_r`,
      `${roomIndex}_u`,
      `${roomIndex}_d`,
      `${roomIndex}_b`,
      `${roomIndex}_f`,
    ];
    var boxMaterials = [];

    arr.forEach((item) => {
      // Á∫πÁêÜÂä†ËΩΩ
      let texture = new THREE.TextureLoader().load(`/textures/room2/${textureUrl}/${item}.jpg`);
      // ÂàõÂª∫ÊùêË¥®
      if (item === `${roomIndex}_u` || item === `${roomIndex}_d`) {
        texture.rotation = Math.PI;
        texture.center = new THREE.Vector2(0.5, 0.5);
        boxMaterials.push(new THREE.MeshBasicMaterial({ map: texture }));
      } else {
        boxMaterials.push(new THREE.MeshBasicMaterial({ map: texture }));
      }
    });
    const cube = new THREE.Mesh(geometry, boxMaterials);
    cube.position.copy(position);
    cube.rotation.copy(euler);
    // cube.geometry.scale(1, 1, -1);
    scene.add(cube);

    THREE.DefaultLoadingManager.onProgress = function (item, loaded, total) {
      const progress = new Number((loaded / total) * 100).toFixed(2);
      progressDiv.innerHTML = `${progress}%`;

      if (progress === '100.00') {
        document.querySelector('.loading').style.display = 'none';
        document.querySelector('.progress').style.display = 'none';
      }
    };
  }
}

function moveTag(name) {
  let positions = {
    ÂÆ¢ÂéÖ: [100, 110],
    Âé®Êàø: [180, 190],
    Èò≥Âè∞: [50, 50],
    ‰∏ªÂçß: [160, 40],
    Ëµ∞Âªä: [150, 90],
  };
  if (positions[name]) {
    gsap.to(tagDiv, {
      duration: 1,
      x: positions[name][0],
      y: positions[name][1],
      ease: "power3.inOut",
    });
  }
}

