let isMouseDown = false;

// ÁõëÂê¨Èº†Ê†áÊåâ‰∏ã‰∫ã‰ª∂
window.addEventListener(
  "mousedown",
  () => {
    isMouseDown = true;
  },
  false
);
// ÁõëÂê¨Èº†Ê†áÊä¨Ëµ∑‰∫ã‰ª∂
window.addEventListener(
  "mouseup",
  () => {
    isMouseDown = false;
  },
  false
);
// ÁõëÂê¨Èº†Ê†áÁßªÂä®‰∫ã‰ª∂
window.addEventListener(
  "mousemove",
  (e) => {
    if (isMouseDown) {
      camera.rotation.y += (e.movementX / window.innerWidth) * Math.PI;
      camera.rotation.x += (e.movementY / window.innerHeight) * Math.PI;
      camera.rotation.order = 'YXZ';
    }
  },
  false
);


const renderer = new THREE.WebGLRenderer()
renderer.setSize(window.innerWidth, window.innerHeight)
container.appendChild(renderer.domElement)


function animate() {
  renderer.render(scene, camera);
  requestAnimationFrame(animate);

}
animate()

window.addEventListener('resize', () => {
  // Êõ¥Êñ∞ÂÆΩÈ´òÊØî
  camera.aspect = window.innerWidth / window.innerHeight
  // Êõ¥Êñ∞ÊëÑÂÉèÊú∫ÊäïÂΩ±Áü©Èòµ
  camera.updateProjectionMatrix();
  // Êõ¥Êñ∞Ê∏≤ÊüìÂô®
  renderer.setSize(window.innerWidth, window.innerHeight)
  // Êõ¥Êñ∞ÂÉèÁ¥†ÊØî
  renderer.setPixelRatio = window.devicePixelRatio;
})